import React from 'react'
import { auth } from '../firebase'

function Signout() {
    return (
        <div>

            
            <span style={ {cursor : "pointer" , fontSize : "larger"}} onClick={ () => auth.signOut()}><i className="fa fa-sign-in" aria-hidden="true"></i></span>
        </div>
    )
}

export default Signout
