import React from 'react';
import "./navbar.css";
// import { Link } from 'react-router-dom';
import { auth } from '../firebase';
import Signin from './Signin';
import Signout from './signout';
import { useAuthState} from 'react-firebase-hooks/auth';

function Footer() {
    const [user]= useAuthState (auth) ;
    return (
        <div>
            <ul id="bottommenu">
                 <li>
                {user ? <Signout/> :  <Signin/>}
                </li>
               { /* <li> 
                    <Link className="navelement" to="/"><i className="fa fa-users" aria-hidden="true"></i></Link>
                </li>
                <li>
                    <Link className="navelement" to="/about"><i className="fa fa-gear" aria-hidden="true"></i></Link>
                </li> */}
                <li > <i className="fa fa-paper-plane" aria-hidden="true"></i></li>
                <li ><i className="fa fa-user-circle-o" aria-hidden="true"></i></li>
            
            </ul>
        </div>
    )
}

export default Footer
